import { mapState, mapActions } from 'vuex'
import weather from './weather'
export default {
	data() {
        return {
            background: ['color1', 'color2', 'color3'],
            indicatorDots: true,
            autoplay: true,
            interval: 2000,
            duration: 500,
						notices: ["国际站4月1日14:00开放MDC/USDT交易市场qqqqqqqqqqqqqqqqqqqqqqqqqqqqqqq","国际站4月2日10:00上线HKL","关于国际站即将上线 GCCT（Global Cash Coin)"]
        }
    },
  mounted() {
    this.initPage()
  },
  components: {
    weather
  },
  computed: {
    ...mapState('home', ['data']),
    ...mapState('weather', ['weather']),
    content() {
      return this.data.hp_content.split('by')[0]
    }
  },
  methods: {
    ...mapActions('home', ['getNewIds', 'getHomeData']),
    async initPage() {
      await this.getNewIds()
      await this.getHomeData()
    }
  }
}
